.projectDiv h2 {
  font-size: 50px;
  transition: 0.5s;
  cursor: pointer; }
  .projectDiv h2:hover {
    color: #E11246; }
  .projectDiv h2 i:before {
    transition: 0.5s; }
  .projectDiv h2 i.rotate:before {
    transform: rotate(90deg); }
  .projectDiv h2 i:hover {
    transform: rotate(90deg); }

.projectDiv .info-container {
  display: grid;
  grid-template-columns: 1fr;
  width: 100%;
  height: 0;
  max-height: 0;
  overflow: hidden;
  transition: 1.5s;
  opacity: 0; }
  .projectDiv .info-container.expanded {
    height: initial;
    max-height: initial;
    padding-bottom: 80px;
    opacity: 1; }
  .projectDiv .info-container img {
    width: 100%; }
  .projectDiv .info-container .screenshot {
    width: 95%;
    position: relative;
    background-size: contain;
    border: 2px solid white;
    border-radius: 10px;
    box-sizing: content-box;
    padding: 0; }
    .projectDiv .info-container .screenshot a {
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      z-index: 10; }
    .projectDiv .info-container .screenshot:before {
      content: "";
      display: block; }
  .projectDiv .info-container .screenshot.snap {
    background-image: url("./ProjectScreenshots/snap-ninja.png"); }
    .projectDiv .info-container .screenshot.snap:before {
      padding-top: 74.7126437%; }
  .projectDiv .info-container .screenshot.swapi {
    background-image: url("./ProjectScreenshots/swapi-box.png"); }
    .projectDiv .info-container .screenshot.swapi:before {
      padding-top: 56.39%; }
  .projectDiv .info-container .screenshot.centipede {
    background-image: url("./ProjectScreenshots/game-time.png"); }
    .projectDiv .info-container .screenshot.centipede:before {
      padding-top: 76.8138801%; }
  .projectDiv .info-container .screenshot.headcount {
    background-image: url("./ProjectScreenshots/headcount.png"); }
    .projectDiv .info-container .screenshot.headcount:before {
      padding-top: 74.765625%; }
  .projectDiv .info-container .screenshot.comp3 {
    background-image: url("./ProjectScreenshots/comp3.png"); }
    .projectDiv .info-container .screenshot.comp3:before {
      padding-top: 75.12%; }
  .projectDiv .info-container .screenshot.tchat {
    background-image: url("./ProjectScreenshots/t-chat.png"); }
    .projectDiv .info-container .screenshot.tchat:before {
      padding-top: 125.8156028%; }
  .projectDiv .info-container .ss-fake-content {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0; }
  .projectDiv .info-container h2 {
    font-size: 50px;
    text-align: center; }
  .projectDiv .info-container h3 {
    font-size: 40px;
    width: 100%;
    margin-bottom: 10px; }
  .projectDiv .info-container p {
    margin-top: 10px;
    width: 36em;
    font-size: 18px; }
  .projectDiv .info-container .techStack {
    padding: 0;
    display: flex;
    flex-wrap: wrap;
    width: 36em;
    margin-bottom: 60px;
    margin-right: auto; }
    .projectDiv .info-container .techStack li {
      width: 50%;
      list-style: none;
      font-size: 40px;
      color: #E11246; }
  .projectDiv .info-container .buttonContainer {
    width: 100%; }
    .projectDiv .info-container .buttonContainer a {
      text-decoration: none;
      margin-top: 30px;
      margin-right: 20px;
      color: white;
      background-color: #E11246;
      padding: 20px;
      border-radius: 5px;
      transition: .2s; }
      .projectDiv .info-container .buttonContainer a:hover {
        color: #E11246;
        background-color: white; }

@media screen and (max-width: 850px) {
  .Projects .projectDiv h2 {
    font-size: 40px; }
  .Projects .projectDiv p {
    width: 100%; }
  .Projects .projectDiv .techStack {
    width: 100%; }
    .Projects .projectDiv .techStack li {
      font-size: 20px; } }
